"use client";
import DashboardLayout from "../Layout/DashboardLayout/page";
import UserLayout from "../Layout/UserLayout/UserLayout";
import React from "react";
import Style from "./profile.module.css";
import { BASE_URL } from "@/config";
import { getAboutUser } from "@/config/redux/action/authAction";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getAllPosts } from "@/config/redux/action/postAction";

export default function Profile() {
  const dispatch = useDispatch();

const clientServer = axios.create({
  baseURL: BASE_URL, // your API base URL
});


  const authState = useSelector((state) => state.auth);
  const [userProfile, setUserProfile] = useState({})
  const [userPosts, setUserPosts] = useState([]);
  const postReducer = useSelector((state) => state.postReducer);

  useEffect(() => {
    dispatch(getAboutUser({ token: localStorage.getItem("token") }));
    dispatch(getAllPosts());
  }, []);
  useEffect(() =>{
     if(authState.user != undefined) {
      setUserProfile(authState.user)
      let post = postReducer.posts.filter((post) =>{
        return post.userId.username === authState.user.userId.username
      })
      console.log(post, authState.user.userId.username)
      setUserPosts(post);
     }

    },[authState.user, postReducer.posts])

    const updateProfilePicture = async (file) =>{
      const formData = new FormData();
      formData.append("profile_picture" , file);
      formData.append("token" , localStorage.getItem("token"));

      const response = await clientServer.post("/update_profile_picture",formData, {
        headers : {
          'Content-Type' : 'multipart/form-data',
        },
      });

      dispatch(getAboutUser({ token: localStorage.getItem("token")}));
    }
  return (
    <UserLayout>
      <DashboardLayout>
        {authState.user && userProfile.userId && (
          <div className={Style.container}>
            <div className={Style.backDropContainer}>
              <label htmlFor='profilePictureUpload' className={Style.backDrop__overlay}>
                <p>Edit</p>
              </label>
              <input onChange={(e) =>{
                updateProfilePicture(e.target.files[0])
              }}hidden type="file" id='profilePictureUpload' />
              <img
                src={
                  userProfile.userId?.profilePicture
                    ? `${BASE_URL}/${user.userId.profilePicture}`
                    : "/images/default.jpg"
                }
                alt=""
              />
            </div>
            <div className={Style.profileContainer__details}>
              <div style={{ display: "flex", gap: "0.7rem" }}>
                <div style={{ flex: "0.8" }}>
                  <div
                    style={{
                      display: "flex",
                      width: "fit-content",
                      alignItems: "center",
                      gap: "1.2rem",
                    }}
                  >
                    <h2>{userProfile.userId.name}</h2>
                    <p style={{ color: "grey" }}>
                      @{userProfile.userId.username}
                    </p>
                  </div>
                  <div>
                    <p>{userProfile.bio}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
      </DashboardLayout>
    </UserLayout>
  );
}
